#iframecontainer {
	top: 0;
	bottom: 0;
	width: 100%;
	margin-left: -50%;
  height: auto;
	display: none;
	position: fixed;
	left:50%;
	background:#FFF;
	border: 1px solid #555;
	box-shadow: 2px 2px 40px #222;
	z-index: 999999;
}
/* Medium devices and up */
@media (min-width: 768px) {
  #iframecontainer {
		top: 15px;
		bottom: 15px;
		width:90%;
		margin-left: -45%;
	}
}
#iframecontainer iframe {
	display:none;
	width: 100%;
	height: 100%;
	position: absolute;
	border: none;
}
#loader {
	background: url(/ywiki-plugins/dist/production/a1cd24ddb8cb8288ce0893da7bbe45e5.gif);
	background-repeat:no-repeat;
	width: 500px;
	height: 500px;
	margin:auto;
}
#block {
	background: #000;
	opacity:0.6;
	position: fixed;
	width: 100%;
	height: 100%;
	top:0;
	left:0;
	display:none;
	z-index: 100;
}
.ciaction .theOneButton {
	position: absolute;
	top: 22px;
	left: 18px;
}
.theOneButton {
  display: inline-block;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  cursor: pointer;
	margin: 10px auto 10px auto;
  padding: 14px 17px;
  outline: none;
  border: 0 none rgb(177,106,2);
  border-bottom: 4px solid rgb(177,106,2);
  -webkit-border-radius: 7px 7px 9px 9px;
  border-radius: 7px 7px 9px 9px;
  font: normal normal bold 22px/26px "Averia Sans Libre", Helvetica, sans-serif;
  color: rgb(255, 255, 255);
  text-decoration: none;
  text-align: center;
  text-transform: uppercase;
  text-indent: 0;
  -o-text-overflow: clip;
  text-overflow: clip;
  letter-spacing: 0;
  white-space: nowrap;
  word-spacing: 0;
  word-wrap: ;
  background: -webkit-linear-gradient(-90deg, rgb(253,218,134) 0, rgb(225,157,60) 100%), rgb(253, 218, 134);
  background: -moz-linear-gradient(180deg, rgb(253,218,134) 0, rgb(225,157,60) 100%), rgb(253, 218, 134);
  background: linear-gradient(180deg, rgb(253,218,134) 0, rgb(225,157,60) 100%), rgb(253, 218, 134);
  background-repeat: repeat;
  background-position: 0 0;
  -webkit-background-origin: padding-box;
  background-origin: padding-box;
  -webkit-background-clip: border-box;
  background-clip: border-box;
  -webkit-background-size: auto auto;
  background-size: auto auto;
  -webkit-box-shadow: 0 -1px 1px 0 rgba(255,255,255,0.701961) inset, 0 1px 1px 0 rgb(177,106,2) ;
  box-shadow: 0 -1px 1px 0 rgba(255,255,255,0.701961) inset, 0 1px 1px 0 rgb(177,106,2) ;
  text-shadow: 0 1px 1px rgba(0,0,0,0.498039) ;
}


.ciaction:hover .theOneButton, .theOneButton:hover {
  background: rgb(225, 157, 60);
  -webkit-transition: all 100ms cubic-bezier(0.42, 0, 0.58, 1);
  -moz-transition: all 100ms cubic-bezier(0.42, 0, 0.58, 1);
  -o-transition: all 100ms cubic-bezier(0.42, 0, 0.58, 1);
  transition: all 100ms cubic-bezier(0.42, 0, 0.58, 1);
}


.ciaction:active .theOneButton, .theOneButton:active {
  margin: 14px 0 0 0;
  border: none;
  -webkit-border-radius: 7px;
  border-radius: 7px;
  background: -webkit-linear-gradient(-90deg, rgb(253,218,134) 0, rgb(225,157,60) 100%), rgb(253, 218, 134);
  background: -moz-linear-gradient(180deg, rgb(253,218,134) 0, rgb(225,157,60) 100%), rgb(253, 218, 134);
  background: linear-gradient(180deg, rgb(253,218,134) 0, rgb(225,157,60) 100%), rgb(253, 218, 134);
  background-repeat: repeat;
  background-position: 0 0;
  -webkit-background-origin: padding-box;
  background-origin: padding-box;
  -webkit-background-clip: border-box;
  background-clip: border-box;
  -webkit-background-size: auto auto;
  background-size: auto auto;
  -webkit-transition: all 30ms cubic-bezier(0.42, 0, 0.58, 1);
  -moz-transition: all 30ms cubic-bezier(0.42, 0, 0.58, 1);
  -o-transition: all 30ms cubic-bezier(0.42, 0, 0.58, 1);
  transition: all 30ms cubic-bezier(0.42, 0, 0.58, 1);
}



// BANNER
/* Extra small devices (phones, less than 768px) */
/* No media query since this is the default in Bootstrap */
.cibanner h1{
	font-size:22px !important;
}
/* Medium devices (desktops, @screen-sm-min and up) */
@media (min-width: 768px) {
  .cibanner h1{
      font-size:24px !important;
  }
}
/* Medium devices (desktops, @screen-md-min and up) */
@media (min-width: 992px) {
  .cibanner h1{
      font-size:36px !important;
  }
}
.cibanner {
	background: -webkit-gradient(linear, 0% 0%, 0% 100%, from(#9cb3d0), to(#c4d2e2));
	background: -webkit-linear-gradient(top, #c4d2e2, #9cb3d0);
	background: -moz-linear-gradient(top, #c4d2e2, #9cb3d0);
	background: -ms-linear-gradient(top, #c4d2e2, #9cb3d0);
	background: -o-linear-gradient(top, #c4d2e2, #9cb3d0);
	border: none;
	display: table;
	width: 100%;
	float: left;
	position: relative;
}
.cibanner h1 {
	font-family: DINWebPro,Arial,sans-serif;
	font-weight: normal;
}
.pullup {
	top: -100px;
	margin-bottom: -90px;
}
.ciaction, .cilogo {
	display: flex;
	float: left;
	position: relative;
}
.valign {
	display:flex;
	align-items: center;
	justify-content: center;
}
.cibanner .ciaction {
	background-color:#fafafc;
	height: 132px;
	width: 132px;
	border: none;
	padding: 0;
	cursor: pointer;
}
.cibanner .ciaction img, .cibanner .cilogo img {
	max-width: 100%;
	max-height: 100%;
	height: auto;
	display:block;
	margin-left: auto;
	margin-right: auto;
}
.cibanner .cicenter {
	height: 132px;
	padding-left:20px;
	padding-right: 20px;
	overflow: hidden;

}
.cicenter h1 {
	color:#ffffff;
	border-top: none !important;
}
.cibanner .cilogo {
	background-color:#7a9bc5;
	height: 132px;
	width: 132px;
	float: right;
}
@font-face {
  font-family: 'FontAwesome';
  src: url('https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/fonts/fontawesome-webfont.eot?v=4.7.0');
  src: url('https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/fonts/fontawesome-webfont.eot?#iefix&v=4.7.0') format('embedded-opentype'), url('https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/fonts/fontawesome-webfont.woff2?v=4.7.0') format('woff2'), url('https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/fonts/fontawesome-webfont.woff?v=4.7.0') format('woff'), url('https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/fonts/fontawesome-webfont.ttf?v=4.7.0') format('truetype'), url('https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/fonts/fontawesome-webfont.svg?v=4.7.0#fontawesomeregular') format('svg');
  font-weight: normal;
  font-style: normal;
}
.fa {
    display: inline-block;
    font: normal normal normal 14px/1 FontAwesome;
    font-size: inherit;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
.fa-stack {
    position: relative;
    display: inline-block;
    width: 2em;
    height: 2em;
    line-height: 2em;
    vertical-align: middle;
}
.fa-stack-2x {
    font-size: 2em;
}
.fa-stack-1x {
    line-height: inherit;
}
.fa-stack-1x, .fa-stack-2x {
    position: absolute;
    left: 0;
    width: 100%;
    text-align: center;
}
.fa-lightbulb-o:before {
    content: "\F0EB";
}
.fa-comment-o:before {
    content: "\F0E5";
}
.btn {
	padding: 6px 10px;
}
.btn .text {
	vertical-align: middle;
	margin-left: 5px;
}
